
import React from 'react';
import { X, CheckCircle2, AlertCircle, Loader2, Download, ImageIcon } from 'lucide-react';
import { CompressedImage } from '../types';

interface Props {
  images: CompressedImage[];
  onRemove: (id: string) => void;
}

const formatSize = (bytes: number) => {
  if (bytes === 0) return '0 B';
  const k = 1024;
  const sizes = ['B', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
};

const ImageList: React.FC<Props> = ({ images, onRemove }) => {
  const modeLabels: Record<string, string> = {
    'lossless': '',
    'balanced': '',
    'aggressive': ''
  };

  return (
    <div className="w-full">
      <table className="w-full text-left border-collapse">
        <thead className="bg-slate-50 sticky top-0 z-10 border-b border-slate-200">
          <tr className="text-[10px] font-bold text-slate-400 uppercase tracking-widest">
            <th className="px-6 py-3 font-bold"></th>
            <th className="px-6 py-3 font-bold"> / </th>
            <th className="px-6 py-3 font-bold"></th>
            <th className="px-6 py-3 font-bold"></th>
            <th className="px-6 py-3 font-bold"></th>
            <th className="px-6 py-3 font-bold text-right"></th>
          </tr>
        </thead>
        <tbody className="divide-y divide-slate-100">
          {images.map((img) => {
            const ratio = img.status === 'completed' 
              ? ((1 - img.compressedSize / img.originalSize) * 100).toFixed(1)
              : null;

            return (
              <tr key={img.id} className="group hover:bg-slate-50/50 transition-colors">
                <td className="px-6 py-4">
                  <div className="w-10 h-10 bg-slate-100 border border-slate-200 overflow-hidden flex-shrink-0 rounded-none">
                    {img.status === 'completed' ? (
                      <img src={img.compressedUrl} alt={img.name} className="w-full h-full object-cover" />
                    ) : (
                      <div className="w-full h-full flex items-center justify-center text-slate-300">
                        <ImageIcon className="w-4 h-4" />
                      </div>
                    )}
                  </div>
                </td>
                <td className="px-6 py-4">
                  <div className="flex flex-col">
                    <span className="text-xs font-bold text-slate-800 truncate max-w-[150px]" title={img.name}>
                      {img.name}
                    </span>
                    <div className="flex gap-2 mt-1">
                      <span className={`text-[8px] px-1 font-black uppercase rounded-none border ${img.format === 'WEBP' ? 'border-blue-200 bg-blue-50 text-blue-600' : 'border-slate-200 bg-slate-50 text-slate-500'}`}>
                        {img.format}
                      </span>
                      {img.modeApplied && (
                        <span className="text-[8px] px-1 font-black bg-slate-800 text-white uppercase rounded-none">
                          {modeLabels[img.modeApplied] || img.modeApplied}
                        </span>
                      )}
                    </div>
                  </div>
                </td>
                <td className="px-6 py-4">
                  <span className="text-xs text-slate-400 font-medium">{formatSize(img.originalSize)}</span>
                </td>
                <td className="px-6 py-4">
                  {img.status === 'completed' ? (
                    <div className="flex items-center gap-2">
                      <span className="text-xs font-bold text-[#003366]">{formatSize(img.compressedSize)}</span>
                      <span className="text-[9px] font-black text-green-600 bg-green-50 px-1 border border-green-100">-{ratio}%</span>
                    </div>
                  ) : (
                    <span className="text-xs text-slate-300">--</span>
                  )}
                </td>
                <td className="px-6 py-4">
                  {img.status === 'compressing' && (
                    <div className="flex items-center gap-2 text-[#003366]">
                      <Loader2 className="w-3 h-3 animate-spin" />
                      <span className="text-[10px] font-bold uppercase tracking-tighter"> {Math.round(img.progress * 100)}%</span>
                    </div>
                  )}
                  {img.status === 'completed' && (
                    <div className="flex items-center gap-1.5 text-green-600">
                      <CheckCircle2 className="w-3.5 h-3.5" />
                      <span className="text-[10px] font-bold uppercase"></span>
                    </div>
                  )}
                  {img.status === 'error' && (
                    <div className="flex items-center gap-1.5 text-red-500">
                      <AlertCircle className="w-3.5 h-3.5" />
                      <span className="text-[10px] font-bold uppercase"></span>
                    </div>
                  )}
                </td>
                <td className="px-6 py-4 text-right">
                  <div className="flex items-center justify-end gap-2 opacity-0 group-hover:opacity-100 transition-opacity">
                    {img.status === 'completed' && (
                      <a 
                        href={img.compressedUrl} 
                        download={`_${img.name.split('.')[0]}.${img.format.toLowerCase()}`}
                        className="p-1.5 text-[#003366] hover:bg-[#003366] hover:text-white border border-[#003366] transition-all"
                        title=""
                      >
                        <Download className="w-3.5 h-3.5" />
                      </a>
                    )}
                    <button 
                      onClick={() => onRemove(img.id)}
                      className="p-1.5 text-slate-300 hover:text-red-500 border border-slate-200 hover:border-red-200 transition-all"
                      title=""
                    >
                      <X className="w-3.5 h-3.5" />
                    </button>
                  </div>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};

export default ImageList;
