
import React from 'react';
import { PieChart, TrendingDown, Package, Database } from 'lucide-react';
import { CompressionStats } from '../types';

interface Props {
  stats: CompressionStats;
}

const formatSize = (bytes: number) => {
  if (bytes === 0) return '0 B';
  const k = 1024;
  const sizes = ['B', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
};

const StatsCard: React.FC<Props> = ({ stats }) => {
  const saved = stats.totalOriginal - stats.totalCompressed;
  const savedRatio = stats.totalOriginal > 0 
    ? ((saved / stats.totalOriginal) * 100).toFixed(1)
    : '0';

  return (
    <div className="bg-white border border-slate-200 p-4 rounded-none shadow-sm relative overflow-hidden">
      {/*  */}
      <div className="absolute top-0 right-0 w-12 h-12 bg-[#003366]/5 -mr-6 -mt-6 rotate-45 pointer-events-none"></div>
      
      <h3 className="text-xs font-bold text-slate-400 uppercase tracking-widest mb-3 flex items-center gap-2">
        <PieChart className="w-3.5 h-3.5" /> 
      </h3>

      <div className="space-y-4 relative z-10">
        <div className="grid grid-cols-2 gap-2">
          <div className="bg-slate-50 border border-slate-100 p-2.5 rounded-none">
            <div className="text-[9px] font-black text-slate-400 uppercase mb-0.5 flex items-center gap-1">
              <Package className="w-2.5 h-2.5" /> 
            </div>
            <div className="text-lg font-black text-slate-800 tracking-tighter leading-none">{stats.filesCount}</div>
          </div>
          <div className="bg-blue-50 border border-blue-100 p-2.5 rounded-none">
            <div className="text-[9px] font-black text-[#003366] uppercase mb-0.5 flex items-center gap-1">
              <TrendingDown className="w-2.5 h-2.5" /> 
            </div>
            <div className="text-lg font-black text-[#003366] tracking-tighter leading-none">-{savedRatio}%</div>
          </div>
        </div>

        <div className="space-y-3">
          <div>
            <div className="flex justify-between text-[9px] font-bold text-slate-400 uppercase mb-1">
              <span></span>
              <span className="text-slate-600">{formatSize(stats.totalOriginal)}</span>
            </div>
            <div className="w-full bg-slate-100 h-1 rounded-none">
              <div className="bg-slate-300 h-full w-full"></div>
            </div>
          </div>

          <div>
            <div className="flex justify-between text-[9px] font-bold text-[#003366] uppercase mb-1">
              <span></span>
              <span className="text-[#003366]">{formatSize(stats.totalCompressed)}</span>
            </div>
            <div className="w-full bg-slate-100 h-1 rounded-none">
              <div 
                className="bg-[#003366] h-full transition-all duration-700 ease-out"
                style={{ width: `${stats.totalOriginal > 0 ? (stats.totalCompressed / stats.totalOriginal) * 100 : 0}%` }}
              ></div>
            </div>
          </div>
        </div>

        <div className="pt-3 border-t border-slate-100 flex items-center gap-2.5">
          <div className="p-1.5 bg-green-50 text-green-700 border border-green-100 rounded-none">
            <Database className="w-3.5 h-3.5" />
          </div>
          <div>
            <div className="text-[9px] font-black text-slate-400 uppercase leading-none mb-1"></div>
            <div className="text-sm font-black text-green-700 leading-none">{formatSize(saved)}</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default StatsCard;
