
import React, { useState, useMemo, useRef } from 'react';
import { 
  FileImage, 
  Layers, 
  Trash2, 
  Download, 
  Zap, 
  Loader2,
  Upload,
  Archive,
  Image as ImageIcon
} from 'lucide-react';
import JSZip from 'jszip';
import imageCompression from 'browser-image-compression';
import { CompressedImage, CompressionStats, CompressionSettings as ISettings } from './types';
import Header from './components/Header';
import StatsCard from './components/StatsCard';
import ImageList from './components/ImageList';
import CompressionSettings from './components/CompressionSettings';

const App: React.FC = () => {
  const [images, setImages] = useState<CompressedImage[]>([]);
  const [isProcessing, setIsProcessing] = useState(false);
  const [isDragging, setIsDragging] = useState(false);
  const [settings, setSettings] = useState<ISettings>({
    mode: 'balanced',
    convertToWebP: true,
    maxWidth: 1920
  });

  const fileInputRef = useRef<HTMLInputElement>(null);

  const stats = useMemo<CompressionStats>(() => {
    const completed = images.filter(img => img.status === 'completed');
    return {
      totalOriginal: completed.reduce((acc, img) => acc + img.originalSize, 0),
      totalCompressed: completed.reduce((acc, img) => acc + img.compressedSize, 0),
      filesCount: completed.length
    };
  }, [images]);

  const getCompressionOptions = (file: File, currentSettings: ISettings) => {
    const { mode, convertToWebP, maxWidth } = currentSettings;
    const options: any = {
      maxSizeMB: 1,
      maxWidthOrHeight: maxWidth,
      useWebWorker: true,
      fileType: convertToWebP ? 'image/webp' : file.type,
    };

    switch (mode) {
      case 'lossless':
        options.maxSizeMB = file.size / (1024 * 1024);
        options.initialQuality = 1.0;
        options.alwaysKeepResolution = true;
        break;
      case 'balanced':
        options.maxSizeMB = Math.min(0.8, (file.size * 0.7) / (1024 * 1024));
        options.initialQuality = 0.8;
        break;
      case 'aggressive':
        options.maxSizeMB = Math.min(0.3, (file.size * 0.4) / (1024 * 1024));
        options.initialQuality = 0.6;
        options.maxWidthOrHeight = 1280;
        break;
    }
    if (file.type === 'image/png' && mode !== 'lossless') {
      options.maxIteration = 15;
    }
    return options;
  };

  const processFile = async (file: File) => {
    const id = Math.random().toString(36).substr(2, 9);
    const newImg: CompressedImage = {
      id,
      name: file.name,
      originalSize: file.size,
      compressedSize: 0,
      originalUrl: URL.createObjectURL(file),
      compressedUrl: '',
      status: 'compressing',
      progress: 0,
      format: file.type.split('/')[1]?.toUpperCase() || 'IMG',
      modeApplied: settings.mode
    };

    setImages(prev => [newImg, ...prev]);

    try {
      const options = {
        ...getCompressionOptions(file, settings),
        onProgress: (p: number) => {
          setImages(prev => prev.map(img => img.id === id ? { ...img, progress: p } : img));
        },
      };

      const compressedFile = await imageCompression(file, options);
      const compressedUrl = URL.createObjectURL(compressedFile);

      setImages(prev => prev.map(img => 
        img.id === id ? { 
          ...img, 
          status: 'completed', 
          compressedSize: compressedFile.size,
          compressedUrl,
          format: compressedFile.type.split('/')[1]?.toUpperCase() || img.format
        } : img
      ));
    } catch (error) {
      console.error('Compression failed:', error);
      setImages(prev => prev.map(img => img.id === id ? { ...img, status: 'error' } : img));
    }
  };

  const handleUpload = async (files: FileList | null) => {
    if (!files || files.length === 0) return;
    setIsProcessing(true);
    const fileList = Array.from(files);
    
    // 
    const batchSize = 3;
    for (let i = 0; i < fileList.length; i += batchSize) {
      const batch = fileList.slice(i, i + batchSize);
      await Promise.all(batch.map(async (file) => {
        //  ZIP 
        if (file.type === 'application/zip' || file.name.toLowerCase().endsWith('.zip')) {
          try {
            const zip = new JSZip();
            const contents = await zip.loadAsync(file);
            const zipFiles = Object.values(contents.files).filter(f => !f.dir && /\.(jpe?g|png|webp)$/i.test(f.name));
            
            //  ZIP 
            for (const zipEntry of zipFiles) {
              const blob = await zipEntry.async('blob');
              const extractedFile = new File([blob], zipEntry.name, { 
                type: 'image/' + (zipEntry.name.split('.').pop()?.toLowerCase() || 'png') 
              });
              await processFile(extractedFile);
            }
          } catch (e) {
            console.error("ZIP :", e);
          }
        } 
        // 
        else if (file.type.startsWith('image/')) {
          await processFile(file);
        }
      }));
    }
    setIsProcessing(false);
  };

  const clearAll = () => {
    images.forEach(img => {
      URL.revokeObjectURL(img.originalUrl);
      if (img.compressedUrl) URL.revokeObjectURL(img.compressedUrl);
    });
    setImages([]);
  };

  const downloadAll = async () => {
    // 
    const completed = images.filter(img => img.status === 'completed');
    if (completed.length === 0) return;

    // 
    if (completed.length === 1) {
      const img = completed[0];
      const link = document.createElement('a');
      link.href = img.compressedUrl;
      const ext = img.format.toLowerCase();
      const dotIndex = img.name.lastIndexOf('.');
      const baseName = dotIndex !== -1 ? img.name.substring(0, dotIndex) : img.name;
      link.download = `${baseName}_.${ext}`;
      link.click();
      return;
    }

    //  ZIP
    const zip = new JSZip();
    const fileNameMap = new Map<string, number>();

    for (const img of completed) {
      try {
        const response = await fetch(img.compressedUrl);
        const blob = await response.blob();
        
        const ext = img.format.toLowerCase();
        const dotIndex = img.name.lastIndexOf('.');
        const baseName = dotIndex !== -1 ? img.name.substring(0, dotIndex) : img.name;
        
        let finalFileName = `${baseName}.${ext}`;
        
        // 
        if (fileNameMap.has(finalFileName)) {
          const count = (fileNameMap.get(finalFileName) || 0) + 1;
          fileNameMap.set(finalFileName, count);
          finalFileName = `${baseName}_(${count}).${ext}`;
        } else {
          fileNameMap.set(finalFileName, 0);
        }

        zip.file(finalFileName, blob);
      } catch (err) {
        console.error(` ${img.name} :`, err);
      }
    }

    const content = await zip.generateAsync({ type: 'blob' });
    const zipUrl = URL.createObjectURL(content);
    const link = document.createElement('a');
    link.href = zipUrl;
    link.download = `__${new Date().getTime()}.zip`;
    link.click();
    
    // 
    setTimeout(() => URL.revokeObjectURL(zipUrl), 60000);
  };

  const removeImage = (id: string) => {
    setImages(prev => {
      const img = prev.find(i => i.id === id);
      if (img) {
        URL.revokeObjectURL(img.originalUrl);
        if (img.compressedUrl) URL.revokeObjectURL(img.compressedUrl);
      }
      return prev.filter(i => i.id !== id);
    });
  };

  const onDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(true);
  };

  const onDragLeave = () => {
    setIsDragging(false);
  };

  const onDrop = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
    handleUpload(e.dataTransfer.files);
  };

  return (
    <div className="h-screen flex flex-col bg-slate-50 overflow-hidden">
      <Header />
      
      <main className="flex-1 overflow-hidden flex flex-row p-4 gap-4">
        {/*  -  */}
        <aside className="w-80 flex flex-col gap-3 overflow-y-auto pr-1">
          <CompressionSettings 
            settings={settings} 
            onSettingsChange={setSettings} 
          />
          
          <div className="bg-white border border-slate-200 p-4 rounded-none">
            <h3 className="text-xs font-bold text-slate-400 uppercase tracking-widest mb-3 flex items-center gap-2">
              <Layers className="w-4 h-4" /> 
            </h3>
            <div className="grid grid-cols-1 gap-2">
              {/* Fix: ensure onClick is assigned to a function, not a boolean */}
              <button 
                onClick={clearAll}
                disabled={images.length === 0}
                className="w-full flex items-center justify-center gap-2 px-4 py-2 bg-slate-100 hover:bg-slate-200 text-slate-600 font-bold text-xs transition-colors disabled:opacity-50"
              >
                <Trash2 className="w-3.5 h-3.5" /> 
              </button>
              <button 
                onClick={downloadAll}
                disabled={images.filter(img => img.status === 'completed').length === 0}
                className="w-full flex items-center justify-center gap-2 px-4 py-2 bg-[#003366] hover:bg-[#002244] text-white font-bold text-xs transition-colors shadow-sm disabled:opacity-50"
              >
                <Download className="w-3.5 h-3.5" /> 
              </button>
            </div>
          </div>
          
          <StatsCard stats={stats} />
        </aside>

        {/*  */}
        <div 
          className={`flex-1 flex flex-col bg-white border border-slate-200 relative overflow-hidden ${isDragging ? 'ring-2 ring-[#003366] ring-inset' : ''}`}
          onDragOver={onDragOver}
          onDragLeave={onDragLeave}
          onDrop={onDrop}
        >
          {images.length === 0 ? (
            <div className="flex-1 flex flex-col items-center justify-center p-12 text-center">
              <div className="w-20 h-20 bg-slate-50 border-2 border-dashed border-slate-200 flex items-center justify-center mb-6">
                <Upload className={`w-8 h-8 ${isDragging ? 'text-[#003366] scale-110' : 'text-slate-300'} transition-all`} />
              </div>
              <h2 className="text-xl font-black text-slate-800 tracking-tight mb-2">
                {isDragging ? '' : ''}
              </h2>
              <p className="text-xs text-slate-400 max-w-xs mx-auto mb-8 font-medium">
                 ZIP 
              </p>
              <button 
                onClick={() => fileInputRef.current?.click()}
                className="bg-[#003366] hover:bg-[#002244] text-white px-8 py-3 font-bold text-xs flex items-center gap-2 transition-all shadow-lg active:scale-95"
              >
                <FileImage className="w-4 h-4" /> 
              </button>
              <input 
                type="file" 
                className="hidden" 
                multiple 
                accept="image/*,.zip" 
                ref={fileInputRef}
                onChange={(e) => handleUpload(e.target.files)}
              />
            </div>
          ) : (
            <div className="flex-1 flex flex-col overflow-hidden">
              <div className="flex-1 overflow-y-auto">
                <ImageList images={images} onRemove={removeImage} />
              </div>
              
              {/*  */}
              <div className="h-10 bg-slate-50 border-t border-slate-200 px-6 flex items-center justify-between">
                <div className="flex items-center gap-4">
                  <span className="text-[10px] font-bold text-slate-400 flex items-center gap-1.5 uppercase tracking-wider">
                    <Archive className="w-3 h-3" /> : {images.length}
                  </span>
                  <span className="text-[10px] font-bold text-slate-400 flex items-center gap-1.5 uppercase tracking-wider">
                    <Zap className="w-3 h-3" /> : {images.filter(i => i.status === 'completed').length}
                  </span>
                </div>
                {isProcessing && (
                  <div className="flex items-center gap-2 text-[#003366]">
                    <Loader2 className="w-3 h-3 animate-spin" />
                    <span className="text-[10px] font-black uppercase tracking-tighter">...</span>
                  </div>
                )}
              </div>
            </div>
          )}
          
          {/*  */}
          {isDragging && (
            <div className="absolute inset-0 bg-[#003366]/5 backdrop-blur-[2px] z-50 flex items-center justify-center pointer-events-none border-4 border-dashed border-[#003366]/20 m-2">
              <div className="bg-white p-6 shadow-2xl flex flex-col items-center">
                <Zap className="w-10 h-10 text-[#003366] mb-3 animate-bounce" />
                <span className="text-xs font-black uppercase tracking-widest text-[#003366]"></span>
              </div>
            </div>
          )}
        </div>
      </main>
    </div>
  );
};

export default App;
