
import React from 'react';
import { Settings2, Zap, ShieldCheck, Target, RefreshCw } from 'lucide-react';
import { CompressionSettings as ISettings, CompressionMode } from '../types';

interface Props {
  settings: ISettings;
  onSettingsChange: (settings: ISettings) => void;
}

const CompressionSettings: React.FC<Props> = ({ settings, onSettingsChange }) => {
  const modes: { id: CompressionMode; label: string; icon: any; desc: string }[] = [
    { 
      id: 'lossless', 
      label: '', 
      icon: ShieldCheck, 
      desc: '' 
    },
    { 
      id: 'balanced', 
      label: '', 
      icon: Target, 
      desc: '' 
    },
    { 
      id: 'aggressive', 
      label: '', 
      icon: Zap, 
      desc: '' 
    },
  ];

  return (
    <div className="bg-white border border-slate-200 p-4 rounded-none shadow-sm">
      <h3 className="text-xs font-bold text-slate-400 uppercase tracking-widest mb-3 flex items-center gap-2">
        <Settings2 className="w-4 h-4" /> 
      </h3>

      <div className="space-y-1.5">
        {modes.map((m) => (
          <button
            key={m.id}
            onClick={() => onSettingsChange({ ...settings, mode: m.id })}
            className={`w-full text-left p-2.5 rounded-none border transition-all flex gap-3 items-center
              ${settings.mode === m.id 
                ? 'border-[#003366] bg-blue-50/50 ring-1 ring-[#003366]' 
                : 'border-slate-100 hover:border-slate-300 bg-white'
              }
            `}
          >
            <div className={`p-1 rounded-none ${settings.mode === m.id ? 'bg-[#003366] text-white' : 'bg-slate-100 text-slate-400'}`}>
              <m.icon className="w-3 h-3" />
            </div>
            <div>
              <div className="text-xs font-bold text-slate-800 leading-none">{m.label}</div>
              <div className="text-[9px] text-slate-400 leading-tight mt-0.5">{m.desc}</div>
            </div>
          </button>
        ))}

        <div className="pt-3 mt-1.5 border-t border-slate-100">
          <label className="flex items-center justify-between cursor-pointer group">
            <div className="flex items-center gap-2">
              <RefreshCw className={`w-3 h-3 ${settings.convertToWebP ? 'text-[#003366] animate-spin-slow' : 'text-slate-400'}`} />
              <span className="text-[11px] font-bold text-slate-700"> WebP</span>
            </div>
            <div className="relative inline-flex items-center">
              <input 
                type="checkbox" 
                className="sr-only peer" 
                checked={settings.convertToWebP}
                onChange={(e) => onSettingsChange({ ...settings, convertToWebP: e.target.checked })}
              />
              <div className="w-8 h-4 bg-slate-200 peer-focus:outline-none rounded-none peer peer-checked:after:translate-x-full after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:rounded-none after:h-3 after:w-3 after:transition-all peer-checked:bg-[#003366]"></div>
            </div>
          </label>
        </div>
      </div>
    </div>
  );
};

export default CompressionSettings;
